<template>
  <div class="wrapper"   v-loading="loading">
    <div class="title">
      <h1>发表文章</h1>
      <div class="info">
        <label for="title">
          <span>标题：</span>
          <input id="title" v-model="title" type="text" placeholder="请输入标题" />
        </label>
      </div>
      <div class="info">
        <label for="tags">
          <span>标签：</span>
          <input v-model="tags" type="text" placeholder="请输入标签，用逗号隔开" />
        </label>
      </div>
      <div class="info">
        <label for="vcode">
          <span>验证码：</span>
          <input id="vcode" v-model="vcode" type="text" placeholder="请输入验证码" />
        </label>
      </div>
      <div class="vcode">
        <label>
          <span></span>
          <img v-on:click="changeVCodeUrl" id="imgCaptcha" v-bind:src="vCodeUrl" alt />
        </label>
      </div>
      <div class="btn">
        <button v-on:click="submit()">提交</button>
      </div>
    </div>
    <div class="editor" ref="editor">
    </div>
  </div>
</template>

<script>
import E from 'wangeditor';
import { addBlog } from '../api/article';

export default {
  data() {
    return {
      editorContent: '',
      title: null,
      tags: null,
      vCodeUrl: '/api/verificationCode',
      vcode: null,
      loading: false,
    };
  },
  methods: {
    getContent() {
      return this.editorContent;
    },
    changeVCodeUrl() {
      this.vCodeUrl = `/api/verificationCode?${new Date().getTime()}`;
    },
    openMessage(messageType, message) {
      this.$message({
        message,
        type: messageType, // success/warning/info/error
      });
    },
    async submit() {
      // 验证属性
      if (!this.title) {
        this.openMessage('warning', '标题不能为空');
        return;
      }
      if (!this.tags) {
        this.openMessage('warning', '标签不能为空');
        return;
      }
      if (!this.editorContent) {
        this.openMessage('warning', '文章不能为空');
        return;
      }
      if (!this.vcode) {
        this.openMessage('warning', '验证码不能为空');
        return;
      }
      // 发送数据
      const data = {
        title: this.title,
        tags: this.tags,
        content: this.getContent(),
        verificationCode: this.vcode,
      };
      // const result = await fetch('/api/blog', {
      //   method: 'PUT', // or 'PUT'
      //   body: JSON.stringify(data), // data can be `string` or {object}!
      //   headers: new Headers({
      //     'Content-Type': 'application/json',
      //   }),
      // })
      //   .then(res => res.json())
      //   .then((response) => {
      //     console.log('Success:', response);
      //     this.editor.txt.html(''); // 清空
      //   })
      //   .catch(error => console.error('Error:', error));
      this.loading = true;
      const result = await addBlog(data);
      // 返回消息
      if (+result.code === 0) {
        this.openMessage('success', result.msg);
        this.changeVCodeUrl();
        this.editor.txt.html(''); // 清空
        this.vcode = null;
        this.title = null;
        this.tags = null;
      } else {
        this.openMessage('error', result.msg);
      }
      this.loading = false;
    },
  },
  mounted() {
    this.editor = new E(this.$refs.editor);
    this.editor.customConfig.onchange = (html) => {
      this.editorContent = html;
    };
    this.editor.customConfig.uploadImgServer = '/api/upload';
    this.editor.customConfig.uploadFileName = 'img';
    this.editor.customConfig.uploadImgHooks = {
      customInsert(insertImg, result) {
        console.log(result);
        const url = result.data;
        insertImg(url);
      },
    };
    this.editor.create();
  },
};
</script>

<style lang="scss" scoped>
.wrapper {
  width: 100%;
}

.title {
  width: 800px;
  margin: 0 auto;
  h1 {
    height: 50px;
    line-height: 50px;
    font-size: 20px;
  }
  .info {
    height: 35px;
  }
  span {
    display: inline-block;
    width: 80px;
  }
  input {
    width: 300px;
  }
  .btn {
    button{
    margin-right: 5px;
    }
  }
}

.editor {
  width: 800px;
  height: 300px;
  margin: 0 auto;
}
</style>
